import {tab,address,recentlyBrowse,city,searchHistory} from 'js/cache'
const state = {
    category: tab.getTab(),   //
    goodsDetails: {},   // 
    userName: '',       // 
    addressInfo: address.getAddress(),    //  
    browse: recentlyBrowse.getBrowse(), // 
    shopOrderList: [],      // vuex
    temporaryAddress: '',   // 
    city: city.getCity(),
    active: 0,  // tabclass
    searchHistoryList: searchHistory.getHistory()   // 
}

export default state


