import axios from 'axios'

// api
export default class Api {
    /**
     * (Home)
     * recommend 
     * search 
     * keeplogin   
     */
    static recommend() {
        return axios.get('/recommend')
    }

    static search(value) {  // value
        return axios.post('/search', {
            value
        })
    }

    static keeplogin() {
        return axios.post('/keeplogin')
    }
    // ===============================================================================================================
    /**
     * (Category)
     * category 
     */
    static category(id) {  // idid
        return axios.get(`/classification?mallSubId=${id}`)
    }

    // ===============================================================================================================
    /**
     * (ShoppingCart)
     * getCard 
     * editCart 
     * deleteShop  
     */
    static getCard() {
        return axios.get(`/getCard`)
    }

    static editCart(count, id, mallPrice) {   //   id 
        return axios.post('/editCart', {
            count,
            id,
            mallPrice
        })
    }

    static deleteShop(id) { // idid
        return axios.post('/deleteShop', id)
    }

    // ===============================================================================================================

    /**
     * (ShoppingPayMent)
     * placeOrder  address:,tel:orderIdidtotalPrice,idDirect:,count:
     */
    static placeOrder({...args}) {
        return axios.post('/order', args)
    }

    // ===============================================================================================================
    /**
     * (Details)
     * goodOne , id:id
     * collection   goods:
     * cancelCollection   id:id
     * isCollection       id:id
     * addShop      //  id:id
     */
    static goodOne(id) {
        return axios.get(`/goods/one?id=${id}`)
    }

    static collection(goods) {
        return axios.post('/collection', goods)
    }

    static cancelCollection(id) {
        return axios.post('/cancelCollection', {id})
    }

    static isCollection(id) {
        return axios.post(`/isCollection`, { id })
    }

    static addShop(id) {
        return axios.post(`/addShop`, { id })
    }
    // ===============================================================================================================

    /**
     * (My)
     * loginOut 
     */
    static loginOut() {
        return axios.post(`/loginOut`)
    }

    // ===============================================================================================================
    /**
     * (user)
     * getAddress  
     * postAddress   name:,tel:address:isDefault
     *                                provincecitycountyaddressDetail
     *                                areaCodeidid
     * deleteAddress   idid
     * getCollection    
     * register         
     * login    
     * getMyOrder  
     */
    static getAddress() {
        return axios.get(`/getAddress`)
    }

    static postAddress({ ...args }) {
        return axios.post(`/address`, args)
    }

    static deleteAddress(id) {
        return axios.post('/deleteAddress',{
            id
        })
    }

    static getCollection() {
        return axios.get(`/collection/list`)
    }

    static register(username,password) {
        return axios.post('/register',{
            username,
            password
        })
    }

    static login(username,password) {
        return axios.post('/login',{
            username,
            password
        })
    }

    static getMyOrder() {
        return axios.get(`/myOrder`)
    }
}


